<template>
  <div>
    <slidecard id="slidecard"/>
    <!-- <h3>正在热映</h3> -->
    <!-- <van-list v-model="loading" :finished="finished" finished-text="没有更多了" @load="onLoad">
      <div v-for="(e,i) in list" :key="i" class="filmli">
        <img :src="e.images.small">
        <p>{{e.title}}</p>
      </div>
    </van-list>-->
    <mubiaolist :addlist="list"/>
    <van-cell-group>
      <van-field v-model="username" clearable label="目标名" left-icon="flag-o" placeholder="请输入用户名"/>

      <van-field
        v-model="value"
        clearable
        label="沙雕指数"
        type="number"
        maxlength=1
        left-icon="star-o"
        placeholder="打分(0-5)"
      />

      <van-field
        v-model="password"
        @click-right-icon="$notify({
         message: '输入：我爱张俊杰',
          duration: 800,
          background: '#1989fa'
         })"
        label="每日口嗨"
        placeholder="请输入信仰"
        left-icon="like-o"
        right-icon="smile-o"
      />
    </van-cell-group>
    <van-button type="warning" @click="addgoal()">添加</van-button>
  </div>
</template>

<script>
import slidecard from "./slidecard";
import mubiaolist from "./mubiaolist";

export default {
  name: "z",
  components: {
    slidecard,
    mubiaolist
  },
  data() {
    return {
      pageindex: 0,
      total: Number,
      username: "",
      password: "",
      value:Number=3,
      newlist: {},
      list: [
        { name: "airpods2", rate: 1 },
        { name: "iPhone11Max", rate: 2 },
        { name: "一双椰子", rate: 3 },
        { name: "存款3-4w", rate: 4 }
      ]
    };
  },
  created() {
    // this.getflimlist();
  },
  methods: {
    // getflimlist() {
    //   this.$http
    //     .get("/api/movie/in_theaters", {
    //       params: {
    //         start: this.pageindex,
    //         count: 22,
    //         city: "南京"
    //       }
    //     })
    //     //成功返回
    //     .then(({ data }) => {
    //       this.list = this.list.concat(data.subjects);
    //       this.total = data.total;
    //     })
    //     //失败返回
    //     .catch(error => {
    //       console.log(error);
    //     });

    // },
    onLoad() {
      // this.pageindex = this.pageindex + this.list.length;
      // alert(this.pageindex);
      // console.log()
      // this.getflimlist();
      // 异步更新数据

      this.loading = false;
      // 数据全部加载完成
      if (this.list.length >= this.total) {
        this.finished = true;
      }
    },
    addgoal() {
      if (!(this.value==1||this.value==2||this.value==3||this.value==4||this.value==5)) {
        this.$notify('指数有误');
        this.value=this.password=this.username=""
        return;
      }else{
        if (this.password!="我爱张俊杰") {
          this.$notify('信仰有误');
          this.value=this.password=this.username=""
          return;
        }else{
          this.list.push({ name: this.username, rate: this.value });
          this.value=this.password=this.username=""
          this.$toast('添加成功');
        }
          
      }
     
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style  scoped>
.van-list {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
}
.filmli {
  width: 30%;
}
.filmli img {
  width: 100%;
  height: 180px;
  padding: 5px;
  box-sizing: border-box;
}
.filmli p {
  margin: 5px;
  font-size: 14px;
  color: #666;
}
h3 {
  padding-left: 10px;
  text-align: left;
  border-left: 5px solid #4682b4;
  margin: 10px 0;
}
</style>
<style>
.van-list__loading {
  width: 100%;
}
.van-nav-bar__title {
  font-size: 20px;
}
.van-nav-bar {
  /* padding: 5px; */
}
.van-list__finished-text,
.van-list__loading-text {
  width: 100%;
}

.van-button--warning {
  margin-top: 10px;
  width: 60%;
}
</style>



