<template>
  <div id="app">
    <van-nav-bar title="2019打卡目标！"/>
    <router-view/>

    <van-tabbar v-model="active">
      <van-tabbar-item icon="home-o" to="/z">z</van-tabbar-item>
      <van-tabbar-item icon="fire-o" dot to="/s">s</van-tabbar-item>
      <van-tabbar-item icon="like-o" info="5" to="/q">q</van-tabbar-item>
      <van-tabbar-item icon="gem-o" info="20" to="/nb">nb</van-tabbar-item>
    </van-tabbar>
  </div>
</template>

<script>
export default {
  name: "App",
  data() {
    return {
     active: 0,
    };
  }
};
</script>

<style>
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  
  /* padding: 38px 0 60px; */
}
body {
  margin: 0;
  padding: 0;
}
.van-nav-bar {
  background: #4682b4 !important;
  color: #f0f8ff;
  padding: 5px;
  position: fixed;
  width: 100%;
  top: 0;
  height: 40px;
}
.van-nav-bar__title {
    color: #f0f8ff;
}
.van-tabbar {
  height: 56px;
}
.van-tabbar-item__icon {
  font-size: 22px;
}
</style>



