import Vue from 'vue'
import Router from 'vue-router'
import z from '../components/tabbar/z.vue'
import s from '../components/tabbar/s.vue'
import q from '../components/tabbar/q.vue'
import nb from '../components/tabbar/nb.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path:"/",
      redirect:"/z"
    },
    {
      path:'/z',
      component:z
    },
    {
      path:'/s',
      component:s
    },
    {
      path:'/q',
      component:q
    },
    {
      path:'/nb',
      component:nb
    }
  ]
})



