<template>
  <van-list>
    <van-cell v-for="(e,i) in addlist" :key="i" :title="e.name">
      <van-rate
        v-model="e.rate"
        icon="like"
        void-icon="like-o"
        disabled-color="rgb(238,87,0)"
        disabled
      />
    </van-cell>
  </van-list>
</template>
<script>
export default {
  name: "mubiaolist",
  data() {
    return {};
  },
  props: ["addlist"],
  methods: {
    onLoad() {}
  },
  created() {}
};
</script>
<style  scoped>
.van-cell__value {
  text-align: left;
}
</style>



